import React, { useContext, useRef, useReducer, useState, useEffect } from 'react'
import { TaskContext, ModalContext, DateTimeNowContext } from './Contexts'
import TaskForm from './TaskForm'
import { DATETIME_OPTIONS, shown_dateTime } from './DateTimeUtils'
import Icon from './Icon'
import Draggable from 'react-draggable'

export default function ItemInList({children}) {
    // console.log('render item in list')

    const modal = useContext(ModalContext)
    const task = useContext(TaskContext)
    const [clicked, setClicked] = useState(false)

    // Dragging
    const nodeRef = useRef(null)
    const [dragState, dispatchDragState] = useReducer(dragStateReducer, {x:0, y:0})
    const mainElement = document.querySelector('main')

    // Using Effects to show modal with modal.setContent, because we want to call setContent after a drag, but we can't directly give it to the drag event as a callback because the dragging happens in renders and React will give a warning about setting state in renders.
    // And we need to call it after a drag instead of setting it as an onClick, because the dragging replaces click events on mobile, so we have to detect a click by checking if the 'drag' began and ended at the same drag position
    useEffect(() => {
            dispatchDragState({type: 'setThisDispatcher', thisDispatcher: dispatchDragState})
        }, [])

    useEffect(
        () => {
            if (clicked) {
                modal.setContent(
                    <TaskForm oldTask={task} handleCloseForm={()=>{modal.setContent(null)}}/>
                )
                setClicked(false)
            }
        },
        // eslint-disable-next-line
        [clicked]
    )

    return (
        <div className='overlap-container bg-primary'>
            <div className='overlap ps-5 my-auto fs-1'>
                <Icon i='bi-check-lg'/>
            </div>
            <Draggable
                axis='x'
                bounds={DRAG_SETTINGS.BOUNDS}
                defaultClassNameDragging={'bg-dark-subtle'}
                onStart={(e, data)=>{
                    dispatchDragState({type: 'onStart', data: data})
                }}
                onDrag={(e, data)=>{
                    mainElement.scrollBy(0,-(data.deltaY))
                    dispatchDragState({type: 'onDrag', data: data})
                }}
                onStop={(e, data)=>{
                    dispatchDragState({
                        type: 'onStop',
                        data: data,
                        onClick: ()=>setClicked(true),
                        onDragSuccess: ()=>{
                            task.setDone()
                            task.write_to_DB()}
                    })
                }}
                nodeRef={nodeRef}
                position={dragState}
                >
                    <div ref={nodeRef} className={'overlap d-flex align-items-center ps-3 pe-2 pt-1 pb-2 bg-secondary-subtle'}>
                        {children}
                    </div>
            </Draggable>
        </div>
    )
}

export const ListItemCheckbox = () => {
    const task = useContext(TaskContext)
    return (
        <div className='me-3'>
            { task.doneDate? <Icon i='bi-check-square-fill'/> : <Icon i='bi-square'/> }
        </div>
    )
}

export const ListItemName = () => {
    const task = useContext(TaskContext)
    return (
        <div className={ ` lh-sm fs-5 me-auto `+ decorate_by_priority(task.priority) }>
            {task.taskName}
        </div>
    )
}

export const ListItemDateTime = ({options=DATETIME_OPTIONS.FULL}) => {
    const task = useContext(TaskContext)
    const today_tomorrow = useContext(DateTimeNowContext)
    return (
        <div className='flex-shrink-0 align-self-start text-body-tertiary'>
            {shown_dateTime(task.dueDate, options, today_tomorrow)}
        </div>
    )
}

export const decorate_by_priority = (p) => {
    const high = 'text-info'
    const medium = ''
    const low = 'text-body-tertiary'
    if (p >= 6) {
        return high
    }
    else if (p >= 3) {
        return medium
    }
    else {
        return low
    }
}

const DRAG_SETTINGS = {
    BOUNDS: {left: 0, right: 125},
    SNAP: {left: 50, right: 75}
}

function dragStateReducer(state, action) {
    const data = action.data
    switch (action.type) {
        case 'setThisDispatcher': {
            state.thisDispatcher = action.thisDispatcher
            return state
        }
        case 'onStart': {
            state.startX = data.x
            state.startY = data.y

            // whether the task should be set to done when the drag reaches onStop
            state.reached_end = false
            return state
        }
        case 'onDrag': {
            if (!state.reached_end && data.x > DRAG_SETTINGS.SNAP.right) {
                // snap to right and set to done
                state.x = DRAG_SETTINGS.BOUNDS.right
                state.reached_end = true
                navigator.vibrate([0, 50, 30])
                state.cancel_click = true // cancel the possibility of interpreting this drag as a click
            }
            else if (state.reached_end && data.x < DRAG_SETTINGS.SNAP.left) {
                // snap to left and undo set to done
                state.x = DRAG_SETTINGS.BOUNDS.left
                state.reached_end = false
                navigator.vibrate([0, 50, 10])
            }
            return state
        }
        case 'onStop': {
            if (!state.cancel_click && data.x === state.startX && data.y === state.startY) {
                action.onClick()
            }
            else {
                // assumes state.thisDispatcher has been initialized with setThisDispatcher
                setTimeout(()=>state.thisDispatcher({type: 'undo_cancel_click'}), 100)
                if (state.reached_end) {
                action.onDragSuccess()
                }
            }
            return state
        }
        case 'undo_cancel_click': {
            state.cancel_click = false
            return state
        }
        default: return state
    }
}